<template><section><h1>Guide 向导/步骤</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">type</code>属性来选择tag的类型，也可以通过<code v-pre class="code_inline">color</code>属性来自定义背景色。</p>
<div class="demo-block">
<q-guide :cur="curStep" :step="stepData">
  <div slot="content">
    <div v-if="curStep==1">
      <p></p>
      <p class="m-t-10">
        <q-button type="primary" @click="curStep=2">下一步</q-button>
      </p>
    </div>
    <div v-if="curStep==2">
      <p></p>
      <p></p>
      <p></p>
      <p class="m-t-10">
        <q-button type="primary" @click="curStep=1">上一步</q-button>
      </p>
      <p class="m-t-10">
        <q-button type="primary" @click="curStep=3">下一步</q-button>
      </p>
    </div>
    <div v-if="curStep==3">
      <p></p>
      <p class="m-t-10">
        <q-button type="primary" @click="curStep=2">上一步</q-button>
      </p>
    </div>
  </div>
</q-guide>

</div>
<demo-block>
                        <div slot="desc"><html><head></head><body></body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">

</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>color</td>
<td>类型</td>
<td>String</td>
<td><code v-pre class="code_inline">primary</code>, <code v-pre class="code_inline">success</code>, <code v-pre class="code_inline">error</code>, <code v-pre class="code_inline">warning</code>, <code v-pre class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>closable</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data() {
      return {
        curStep: 1,
        stepData: [
          {step: 1, before: "第一步内容", after: "完成第一步"},
          {step: 2, before: "第二步内容", after: "完成第二步"},
          {step: 3, before: "第三步内容", after: "完成第三步"},
        ]
      };
    }
  }
</script>


