<div class="demo-block">
  <p>
    <q-switch v-model="checkedA"></q-switch>
  </p>
  <p>
    <q-switch v-model="checkedB">
      <span slot="open">开启</span>
      <span slot="close">关闭</span>
    </q-switch>
  </p>
  <p>
    <q-switch v-model="checkedC">
      <span slot="open">ON</span>
      <span slot="close">OFF</span>
    </q-switch>
  </p>
  
</div>
