<template><section><h1>Tootip 文字提示</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">type</code>属性来控制是哪种类型，通过<code v-pre class="code_inline">text</code>属性来自定义文字。</p>
<div class="demo-block">
  <span style="margin-right:30px;">
    <q-tooltip placement="left" content="我们挥霍的一切必将在最高处汇合">
      <button class="q-btn" type="button">左边显示</button>
    </q-tooltip>
  </span>
  <span style="margin-right:30px;">
    <q-tooltip placement="right" content="我们挥霍的一切必将在最高处汇">
      <button class="q-btn" type="button">右边显示</button>
    </q-tooltip>
  </span>
  <span style="margin-right:30px;">
    <q-tooltip placement="top" content="我们挥霍的一切必将在最高处">
      <button class="q-btn" type="button">顶部显示</button>
    </q-tooltip>
  </span>
  <span style="margin-right:30px;">
    <q-tooltip placement="bottom" content="我们挥霍的一切必将">
      <button class="q-btn" type="button">底部显示</button>
    </q-tooltip>
  </span>
</div>

<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <q-tooltip :placement="'left'" :content="'要显示的内容'">
    <button class="ys-btn" type="button">左边显示</button>
  </q-tooltip>
</div>

</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">q-tooltip</span> <span class="hljs-attr">:placement</span>=<span class="hljs-string">"'left'"</span> <span class="hljs-attr">:content</span>=<span class="hljs-string">"'要显示的内容'"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ys-btn"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span>&gt;</span>左边显示<span class="hljs-tag">&lt;/<span class="hljs-name">button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">q-tooltip</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>text</td>
<td>类型</td>
<td>String</td>
<td><code v-pre class="code_inline">primary</code>, <code v-pre class="code_inline">success</code>, <code v-pre class="code_inline">error</code>, <code v-pre class="code_inline">warning</code>, <code v-pre class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>width</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>range</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>sep</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data() {
      return {
        curValue:"2018-05-22 18:22:55"
      };
    }
  }
</script>


