<template><section>

<h1>Icon 图标</h1>
<h3>如何使用</h3>
<p>使用 <code v-pre class="code_inline">class=&quot;icon&quot;</code> 来声明图标，具体图标的名称请 <code v-pre class="code_inline">copy</code> 相应的标签</p>
<div class="demo-block">
  <i class="ys-icon icon-edit"></i>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><i class="ys-icon icon-edit"></i>

</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ys-icon icon-edit"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>图标示例</h3>
<ul class="icon-list">
  <li v-for="name in icons" :key="name">
    <span>
      <i :class="'ys-icon ' + name"></i>
      {{name}}
    </span>
  </li>
</ul>
</section></template>
<style lang="less">
  .demo-icon .source > i {
    font-size: 18px;
    color: #fff;
    margin: 0 20px;
    vertical-align: middle;
  }
  
  .demo-icon .source > button {
    margin: 0 20px;
  }

  .icon-list {
    overflow: hidden;
    list-style: none;
    padding: 0;
    border: solid 1px rgba(255,255,255,0.08);
    border-radius: 4px;
  }
  .icon-list li {
    float: left;
    width: 10%;
    text-align: center;
    height: 120px;
    line-height: 120px;
    font-size: 13px;
    transition: color .15s linear;

    border-right: 1px solid rgba(255,255,255,0.08);
    border-bottom: 1px solid rgba(255,255,255,0.08);
    margin-right: -1px;
    margin-bottom: -1px;
    span {
      display: inline-block;
      line-height: normal;
      vertical-align: middle;
      font-family: 'Helvetica Neue',Helvetica,'PingFang SC','Hiragino Sans GB','Microsoft YaHei',SimSun,sans-serif;
      color: #fff;
    }
    i {
      display: block;
      font-size: 24px;
      margin-bottom: 15px;
      color: #4a92ff;
    }
    &:hover {
      color: rgb(92, 182, 255);
    }
  }
</style>
<script>
  var iconList = require('../icon.json');

  export default {
    data() {
      return {
        icons: iconList
      };
    }
  }
</script>


