<template><section><h1>radar-chart 雷达图组件</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">type</code>属性来控制是哪种类型，通过<code v-pre class="code_inline">text</code>属性来自定义文字。</p>
<div class="demo-block">
  <q-radar-chart :id="'big-radar'+index" :height="'300px'" :x="xData" :series="seriesData"></q-radar-chart>
</div>

<demo-block>
                        <div slot="desc"><html><head></head><body></body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>text</td>
<td>类型</td>
<td>String</td>
<td><code v-pre class="code_inline">primary</code>, <code v-pre class="code_inline">success</code>, <code v-pre class="code_inline">error</code>, <code v-pre class="code_inline">warning</code>, <code v-pre class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>width</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>range</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>sep</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data() {
      return {
        xData:[
          { name: '销售', max: 6500},
          { name: '管理', max: 16000}, // 标签设置为红色
          { name: '技术', max: 30000},
          { name: '客服', max: 38000},
          { name: '研发', max: 52000},
          { name: '市场', max: 25000}
        ],
        seriesData:[
          {
            value : [4300, 10000, 28000, 30000, 40000, 19000],
            name : '预算分配（Allocated Budget）'
          },
        ]
      };
    },
    mounted() {
      this.lineData.x=["11:22","22:11","22:12","22:13","22:14"];
      this.lineData.series[0].data=["11","22","33","8","15"];
      this.lineData.series[1].data=["5","16","33","8","15"];
    },
  }
</script>


