<template><section><h1>pie-chart 折线图组件</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">type</code>属性来控制是哪种类型，通过<code v-pre class="code_inline">text</code>属性来自定义文字。</p>
<div>
  <q-pie-chart :id="'oooos-percent1'" :height="'240px'" :name="'操作系统'" :y="pieData" :color="1"></q-pie-chart>
  <q-pie-chart :id="'oooos-percent2'" :height="'240px'" :name="'严重程度'" :y="pieData1" :color="2"></q-pie-chart>    
</div>

<demo-block>
                        <div slot="desc"><html><head></head><body></body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>text</td>
<td>类型</td>
<td>String</td>
<td><code v-pre class="code_inline">primary</code>, <code v-pre class="code_inline">success</code>, <code v-pre class="code_inline">error</code>, <code v-pre class="code_inline">warning</code>, <code v-pre class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>width</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>range</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>sep</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data() {
      return {
        pieData:[],
        pieData1:[],
      };
    },
    mounted() {
      this.pieData=[
        {name:"移动",value:"111"},{name:"联通",value:"90"},{name:"小灵通",value:"80"}
      ]
      this.pieData1=[
        {name:"严重",value:"111"},{name:"高",value:"90"},{name:"中",value:"80"}
      ]
    },
  }
</script>


