<template><section><h1>tab 标签选择组件</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">type</code>属性来控制是哪种类型，通过<code v-pre class="code_inline">text</code>属性来自定义文字。</p>
<div class="demo-block">
  <q-tab v-model="curTab" :list="tabList" @change="changeTab()"></q-tab>
  <span>{{curTabText}}</span>
</div>

<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <q-tab v-model="curTab" :list="tabList" @change="changeTab()"></q-tab>
</div>


</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">q-tab</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"curTab"</span>
          <span class="hljs-attr">:list</span>=<span class="hljs-string">"tabList"</span>
          <span class="hljs-attr">@change</span>=<span class="hljs-string">"changeTab()"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">q-tab</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
