        <span class="hljs-attr">tabList</span>:[
          {<span class="hljs-attr">id</span>:<span class="hljs-number">1</span>,<span class="hljs-attr">name</span>:<span class="hljs-string">"最近1小时"</span>},
          {<span class="hljs-attr">id</span>:<span class="hljs-number">24</span>,<span class="hljs-attr">name</span>:<span class="hljs-string">"最近24小时"</span>},
          {<span class="hljs-attr">id</span>:<span class="hljs-number">168</span>,<span class="hljs-attr">name</span>:<span class="hljs-string">"最近7天"</span>},
