<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data() {
      return {
        leftData:[{id:0,name:"1111",selected:false},{id:1,name:"1111",selected:false}],
        rightData:[{id:3,name:"1111",selected:false},],
      };
