<template><section><h1>ring-chart 占比圆环图组件</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">type</code>属性来控制是哪种类型，通过<code v-pre class="code_inline">text</code>属性来自定义文字。</p>
<div class="demo-block" style="overflow: auto">
  <div style="width:300px;float:left;margin-right:20px;">
    <q-ring-chart :id="'host-online-percent-2'" :height="'300px'" :turn="true" :y="11"></q-ring-chart>
  </div>
  <div style="width:300px;float:left;margin-right:50px;">
    <q-ring-chart :id="'host-online-percent-3'" :height="'300px'" :turn="true" :y="50"></q-ring-chart>
  </div>
  <div style="width:300px;float:left;">
    <q-ring-chart :id="'host-online-percent-4'" :height="'300px'" :turn="true" :y="77"></q-ring-chart>
  </div>                
</div>

<demo-block>
                        <div slot="desc"><html><head></head><body></body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>text</td>
<td>类型</td>
<td>String</td>
<td><code v-pre class="code_inline">primary</code>, <code v-pre class="code_inline">success</code>, <code v-pre class="code_inline">error</code>, <code v-pre class="code_inline">warning</code>, <code v-pre class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>width</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>range</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>sep</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data() {
      return {
        barData:{x:[],y:[]},
      };
    },
    mounted() {
      this.barData={
        x:[270000,302,20003,130,2700],
        y:["aa.com","bb.com","cc","dd","ee"]
      }
    },
  }
</script>


