<template><section>
<h1>Tagator 标签输入</h1>
<hr>
<p>用于页面中展示重要的提示信息。</p>
<h3>基本用法</h3>
<p>页面中的非浮层元素，不会自动消失。</p>
<div class="demo-block">
  <q-tagator :width="400" :list="tagList"></q-tagator>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <w-alert type="success">成功提示的文案</w-alert>
  <w-alert type="info">消息提示的文案</w-alert>
  <w-alert type="warning" title="警告提示的文案"></w-alert>
  <w-alert type="error" title="错误提示的文案"></w-alert>
</div>

</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span>&gt;</span>成功提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span>&gt;</span>消息提示的文案<span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"警告提示的文案"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">w-alert</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"error"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"错误提示的文案"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">w-alert</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题，必选参数。也可通过默认 slot 传入</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>type</td>
<td>主题</td>
<td>string</td>
<td>success/warning/info/error</td>
<td>info</td>
</tr>
<tr>
<td>description</td>
<td>辅助性文字</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>closable</td>
<td>是否可关闭</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>center</td>
<td>文字是否居中</td>
<td>boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>close-text</td>
<td>关闭按钮自定义文本</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>show-icon</td>
<td>是否显示图标</td>
<td>boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3>Event</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>关闭alert时触发的事件</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>

<script>
 export default {
    data() {
      return {
        tagList:[1,2,34,56,7],
      };
    }
  }
</script>


