<template><section><h1>Radio 单选按钮</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">list</code>属性来显示列表，通过<code v-pre class="code_inline">value</code>属性来控制当前选中。</p>
<div class="demo-block">
  <q-radio :list="radioData" v-model="curRadio"></q-radio>
</div>

<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <q-radio :list="radioData" :value="curRadio"></q-radio>
</div>

</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">q-radio</span> <span class="hljs-attr">:list</span>=<span class="hljs-string">"radioData"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"curRadio"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">q-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">radioData</span>:[
          {<span class="hljs-attr">id</span>:<span class="hljs-number">1</span>,<span class="hljs-attr">text</span>:<span class="hljs-string">"个人"</span>},
          {<span class="hljs-attr">id</span>:<span class="hljs-number">2</span>,<span class="hljs-attr">text</span>:<span class="hljs-string">"公司"</span>}
        ],
        <span class="hljs-attr">curRadio</span>:<span class="hljs-number">1</span>
      };
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>禁用状态</h3>
<p>由<code v-pre class="code_inline">disabled</code>属性来控制是否禁用。</p>
<div class="demo-block">
  <div>
    <q-radio :list="radioList" :value="curNewRadio"></q-radio>
  </div>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <q-radio :list="radioData" :value="curRadio"></q-radio>
</div>

  
</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">q-radio</span> <span class="hljs-attr">:list</span>=<span class="hljs-string">"radioData"</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"curRadio"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">q-radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">radioData</span>:[
          {<span class="hljs-attr">id</span>:<span class="hljs-number">1</span>,<span class="hljs-attr">text</span>:<span class="hljs-string">"个人"</span>,<span class="hljs-attr">disabled</span>:<span class="hljs-literal">true</span>},
          {<span class="hljs-attr">id</span>:<span class="hljs-number">2</span>,<span class="hljs-attr">text</span>:<span class="hljs-string">"公司"</span>}
        ],
        <span class="hljs-attr">curRadio</span>:<span class="hljs-number">2</span>
      };
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
  
</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>list</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>类型</td>
<td>String</td>
<td><code v-pre class="code_inline">primary</code>, <code v-pre class="code_inline">success</code>, <code v-pre class="code_inline">error</code>, <code v-pre class="code_inline">warning</code>, <code v-pre class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>disabled</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data() {
      return {
        radioData:[
          {id:1,text:"个人"},
          {id:2,text:"公司"}
        ],
        curRadio:1,
        radioList:[
          {id:1,text:"个人",disabled:true},
          {id:2,text:"公司"}
        ],
        curNewRadio:2
      };
    }
  }
</script>


