<template><section><h1>Calendar 日历组件</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">type</code>属性来控制是哪种类型，通过<code v-pre class="code_inline">text</code>属性来自定义文字。</p>
<div class="demo-block">
  <q-calendar :type="'datetime'" :text="'日期时间组件'" v-model="curValue"></q-calendar>
  <q-calendar :type="'date'" :text="'日期组件'"></q-calendar>
  <q-calendar :type="'time'" :text="'时间组件'"></q-calendar>
</div>

<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <q-checkbox text="行者孙" show="checkStatus"></q-checkbox>
</div>
export default {
    data() {
      return {
        checkStatus:true
      };
    }
  }
</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">q-checkbox</span> <span class="hljs-attr">text</span>=<span class="hljs-string">"行者孙"</span> <span class="hljs-attr">show</span>=<span class="hljs-string">"checkStatus"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">q-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
export default {
    data() {
      return {
        checkStatus:true
      };
    }
  }
</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>text</td>
<td>类型</td>
<td>String</td>
<td><code v-pre class="code_inline">primary</code>, <code v-pre class="code_inline">success</code>, <code v-pre class="code_inline">error</code>, <code v-pre class="code_inline">warning</code>, <code v-pre class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>width</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>value</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>range</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>sep</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data() {
      return {
        curValue:"2018-05-22 18:22:55"
      };
    }
  }
</script>


