<template><section><h1>Checkbox 单选按钮</h1>
<hr>
<h3>基础用法</h3>
<p>由<code v-pre class="code_inline">show</code>属性来控制是否选中，通过<code v-pre class="code_inline">text</code>属性来自定义文字。</p>
<div class="demo-block">
  <span style="margin-right:8px;">
    <q-checkbox text="行者孙" v-model="checkStatus"></q-checkbox>
  </span>
  <span>
  <span style="margin-right:8px;">
    <q-checkbox text="孙行者"></q-checkbox>
  </span>
</span></div>

<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <q-checkbox v-model="checkStatus" text="行者孙"></q-checkbox>
</div>
export default {
    data() {
      return {
        checkStatus:true
      };
    }
  }
</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">q-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkStatus"</span> <span class="hljs-attr">text</span>=<span class="hljs-string">"行者孙"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">q-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
export default {
    data() {
      return {
        checkStatus:true
      };
    }
  }
</code></pre>
</div></demo-block>
<h3>禁用状态</h3>
<p>由<code v-pre class="code_inline">disabled</code>属性来控制是否禁用。</p>
<div class="demo-block">
  <div>
    <q-checkbox text="者行孙" v-model="checkStatus" disabled></q-checkbox>
  </div>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><div>
  <q-checkbox text="者行孙" v-model="checkStatus" disabled></q-checkbox>
</div>
  
</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">q-checkbox</span> <span class="hljs-attr">text</span>=<span class="hljs-string">"者行孙"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checkStatus"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">q-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  
</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>text</td>
<td>类型</td>
<td>String</td>
<td><code v-pre class="code_inline">primary</code>, <code v-pre class="code_inline">success</code>, <code v-pre class="code_inline">error</code>, <code v-pre class="code_inline">warning</code>, <code v-pre class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>disabled</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>close</td>
<td>点击关闭按钮时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data() {
      return {
        checkStatus:true
      };
    }
  }
</script>


